.jst-transition {
  transition-timing-function: ease;
  transition-duration: .3s;
}
.jst-fade-enter-active,
.jst-fade-leave-active {
  transition-property: opacity;
  transition: all 0.3s;
}
.jst-fade-enter,
.jst-fade-leave-to {
  opacity: 0;
}
.jst-fade-up-enter-active,
.jst-fade-up-leave-active,
.jst-fade-down-enter-active,
.jst-fade-down-leave-active,
.jst-fade-left-enter-active,
.jst-fade-left-leave-active,
.jst-fade-right-enter-active,
.jst-fade-right-leave-active {
  transition-property: opacity, transform;
  transition: all 0.3s;
}
.jst-fade-up-enter,
.jst-fade-up-leave-to {
  opacity: 0;
  transform: translate3d(0, 100%, 0);
}
.jst-fade-down-enter,
.jst-fade-down-leave-to {
  opacity: 0;
  transform: translate3d(0, -100%, 0);
}
.jst-fade-left-enter,
.jst-fade-left-leave-to {
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
}
.jst-fade-right-enter,
.jst-fade-right-leave-to {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}
.jst-slide-up-enter-active,
.jst-slide-up-leave-active,
.jst-slide-down-enter-active,
.jst-slide-down-leave-active,
.jst-slide-left-enter-active,
.jst-slide-left-leave-active,
.jst-slide-right-enter-active,
.jst-slide-right-leave-active {
  transition-property: transform;
  transition: all 0.3s;
}
.jst-slide-up-enter,
.jst-slide-up-leave-to {
  transform: translate3d(0, 100%, 0);
}
.jst-slide-down-enter,
.jst-slide-down-leave-to {
  transform: translate3d(0, -100%, 0);
}
.jst-slide-left-enter,
.jst-slide-left-leave-to {
  transform: translate3d(-100%, 0, 0);
}
.jst-slide-right-enter,
.jst-slide-right-leave-to {
  transform: translate3d(100%, 0, 0);
}


