.jst-tab {
  flex: 1;
  cursor: pointer;
  min-width: 0;
  padding: 0 5px;
  font-size: 14px;
  position: relative;
  color: #000;
  line-height: 44px;
  text-align: center;
  box-sizing: border-box;
}
.jst-tab span {
  display: block;
}
.jst-tab--active {
  font-weight: 500;
  color: #323233;
}
.jst-tab--disabled {
  color: #c8c9cc;
}
.jst-tabs {
  position: relative;
}
.jst-tabs__wrap {
  top: 0;
  left: 0;
  right: 0;
  //z-index: 99;
  z-index: 1;
  overflow: hidden;
  position: absolute;
}
.jst-tabs__wrap--page-top {
  position: fixed;
}
.jst-tabs__wrap--content-bottom {
  top: auto;
  bottom: 0;
}
.jst-tabs__wrap--scrollable .jst-tab {
  flex: 0 0 22%;
}
.jst-tabs__wrap--scrollable .jst-tab--complete {
  flex: 1 0 auto;
}
.jst-tabs__wrap--scrollable .jst-tabs__nav {
  overflow: hidden;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
}
.jst-tabs__wrap--scrollable .jst-tabs__nav::-webkit-scrollbar {
  display: none;
}
.jst-tabs__nav {
  display: flex;
  user-select: none;
  position: relative;
  background-color: #fff;
}
.jst-tabs__nav--line {
  height: 100%;
  padding-bottom: 15px;
  /* 15px padding to hide scrollbar in mobile safari */
  box-sizing: content-box;
}
.jst-tabs__nav--card {
  margin: 0 15px;
  border-radius: 2px;
  box-sizing: border-box;
  border: 1px solid #c3c3c3;
  height: 30px;
}
.jst-tabs__nav--card .jst-tab {
  color: #000;
  //border-right: 1px solid #f44;
  line-height: 28px;
}
.jst-tabs__nav--card .jst-tab:last-child {
  border-right: none;
}
.jst-tabs__nav--card .jst-tab.jst-tab--active {
  color: #fff;
  background-color: $primary-color;
}
.jst-tabs__nav--card .jst-tab--disabled {
  color: #c8c9cc;
}
.jst-tabs__line {
  z-index: 1;
  left: 0;
  bottom: 15px;
  height: 3px;
  position: absolute;
  border-radius: 3px;
  background-color: #3aa1ff;
}
.jst-tabs__content--animated {
  overflow: hidden;
}
.jst-tabs__track {
  position: relative;
  display: flex;
  width: 100%;
  height: 100%;
  will-change: left;
}
.jst-tabs--line {
  padding-top: 44px;
}
.jst-tabs--line .jst-tabs__wrap {
  height: 44px;
}
.jst-tabs--card {
  padding-top: 30px;
}
.jst-tabs--card > .jst-tabs__wrap {
  height: 30px;
}
.jst-tabs .jst-tab__pane {
  width: 100%;
  flex-shrink: 0;
  box-sizing: border-box;
}
