.jst-steps {
  overflow: hidden;
  background-color: #fff;
}
.jst-steps--horizontal {
  padding: 10px 10px 0;
}
.jst-steps--horizontal .jst-steps__items {
  display: flex;
  margin: 0 0 10px;
  position: relative;
  padding-bottom: 22px;
}
.jst-steps--vertical {
  padding: 0 0 0 35px;
}




.jst-step {
  flex: 1;
  font-size: 14px;
  position: relative;
  color: #969799;
}
.jst-step__circle {
  display: block;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background-color: #969799;
}
.jst-step--horizontal {
  float: left;
}
.jst-step--horizontal:first-child .jst-step__title {
  transform: none;
  margin-left: 0;
}
.jst-step--horizontal:last-child {
  position: absolute;
  right: 1px;
  width: auto;
}
.jst-step--horizontal:last-child .jst-step__title {
  transform: none;
  margin-left: 0;
}
.jst-step--horizontal:last-child .jst-step__circle-container {
  left: auto;
  right: -9px;
}
.jst-step--horizontal .jst-step__circle-container {
  position: absolute;
  top: 30px;
  left: -8px;
  padding: 0 8px;
  z-index: 1;
  background-color: #fff;
  transform: translateY(-50%);
}
.jst-step--horizontal .jst-step__title {
  display: inline-block;
  font-size: 12px;
  margin-left: 3px;
  transform: translateX(-50%);
}
@media (max-width: 321px) {
  .jst-step--horizontal .jst-step__title {
    font-size: 11px;
  }
}
.jst-step--horizontal .jst-step__line {
  position: absolute;
  left: 0;
  top: 30px;
  width: 100%;
  height: 1px;
  background-color: #ebedf0;
}
.jst-step--horizontal .jst-step__icon {
  display: block;
  font-size: 12px;
}
.jst-step--horizontal .jst-step--process {
  color: #323233;
}
.jst-step--vertical {
  float: none;
  display: block;
  font-size: 14px;
  line-height: 18px;
  padding: 10px 10px 10px 0;
}
.jst-step--vertical:not(:last-child)::after {
  border-bottom-width: 1px;
}
.jst-step--vertical:first-child::before {
  content: '';
  position: absolute;
  width: 1px;
  height: 20px;
  background-color: #fff;
  top: 0;
  left: -15px;
  z-index: 1;
}
.jst-step--vertical .jst-step__circle-container {
  position: absolute;
  z-index: 2;
  top: 19px;
  left: -15px;
  line-height: 1;
  font-size: 12px;
  transform: translate(-50%, -50%);
}
.jst-step--vertical .jst-step__line {
  position: absolute;
  top: 16px;
  left: -15px;
  width: 1px;
  height: 100%;
  background-color: #ebedf0;
}
.jst-step:last-child .jst-step__line {
  width: 0;
}
.jst-step--finish {
  color: #323233;
}
.jst-step--finish .jst-step__circle,
.jst-step--finish .jst-step__line {
  background-color: #3aa1ff;
}

.jst-step--finish .jst-step__icon{
  color: #3aa1ff;
}
