.jst-number-keyboard {
  left: 0;
  bottom: 0;
  width: 100%;
  position: fixed;
  user-select: none;
  background-color: #fff;
  animation-timing-function: ease-out;
}
.jst-number-keyboard__title {
  height: 30px;
  font-size: 14px;
  line-height: 30px;
  text-align: center;
  position: relative;
  color: #7d7e80;
}
.jst-number-keyboard__title-left {
  left: 0;
  position: absolute;
}
.jst-number-keyboard__body {
  box-sizing: border-box;
}
.jst-number-keyboard__close {
  right: 0;
  color: #1989fa;
  font-size: 14px;
  padding: 0 15px;
  position: absolute;
}
.jst-number-keyboard__close:active {
  background-color: #f2f3f5;
}
.jst-number-keyboard__sidebar {
  top: 0;
  right: 0;
  width: 25%;
  position: absolute;
  height: 216px;
}
.jst-number-keyboard--custom .jst-number-keyboard__body {
  padding-right: 25%;
}
//.jst-number-keyboard--safe-area-inset-bottom {
//  padding-bottom: constant(safe-area-inset-bottom);
//  padding-bottom: env(safe-area-inset-bottom);
//}
.jst-key {
  width: 33.33333333%;
  font-size: 24px;
  font-style: normal;
  text-align: center;
  display: inline-block;
  vertical-align: middle;
  height: 54px;
  line-height: 54px;
}
.jst-key::after {
  border-width: 1px 1px 0 0;
}
.jst-key--middle {
  width: 66.66666667%;
}
.jst-key--big {
  width: 100%;
  height: 108px;
  line-height: 108px;
}
.jst-key--blue,
.jst-key--delete {
  font-size: 16px;
}
.jst-key--blue {
  color: #fff;
  background-color: #3aa1ff;
}
.jst-key--blue.jst-key--active {
  background-color: #1989fa;
}
.jst-key--blue::after {
  border-color: #1989fa;
}
.jst-key--gray {
  background-color: #ebedf0;
}
.jst-key--active {
  background-color: #f2f3f5;
}
