.jst-stepper {
    font-size:0
}
.jst-stepper__minus,
.jst-stepper__plus {
    width: 28px;
    height: 28px;
    box-sizing: border-box;
    background-color: #3aa1ff;
    border: 0;
    margin: 1px 5px;
    position: relative;
    padding: 5px;
    vertical-align: middle;
    display:inline-block;
}
.jst-stepper__minus::before,
.jst-stepper__plus::before {
    width:9px;
    height:1px
}
.jst-stepper__minus::after,
.jst-stepper__plus::after {
    width:1px;
    height:9px
}
.jst-stepper__minus::after,
.jst-stepper__minus::before,
.jst-stepper__plus::after,
.jst-stepper__plus::before {
    content:"";
    position:absolute;
    top:0;
    right:0;
    bottom:0;
    left:0;
    margin:auto;
    background-color:#fff
}
.jst-stepper__minus:active,
.jst-stepper__plus:active {
    background-color: #1372c9;
}

.jst-stepper__minus--disabled,
.jst-stepper__plus--disabled {
    background-color: transparent;
    border: 1px solid #c3c3c3;
}
.jst-stepper__minus--disabled::after,
.jst-stepper__minus--disabled::before,
.jst-stepper__plus--disabled::after,
.jst-stepper__plus--disabled::before {
    background-color: #c3c3c3;
}
.jst-stepper__minus--disabled:active,
.jst-stepper__plus--disabled:active {
    background-color: #f7f8fa;
}
//.jst-stepper__minus {
//    border-radius:4px 0 0 4px
//}
.jst-stepper__minus,
.jst-stepper__plus{
    border-radius: 4px;
}
.jst-stepper__minus::after {
    display:none
}
//.jst-stepper__plus {
//    border-radius:0 4px 4px 0
//}
.jst-stepper__input {
    width: 30px;
    height: 28px;
    padding: 1px;
    border: 1px solid #c3c3c3;
    margin: 1px;
    background-color: transparent;
    border-radius: 0;
    box-sizing: border-box;
    color: #000;
    font-size: 14px;
    vertical-align: middle;
    text-align: center;
    -webkit-appearance: none;
    display:inline-block;
}
// type=number input
.jst-stepper__input::-webkit-outer-spin-button,
.jst-stepper__input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    -moz-appearance: textfield;
}


.jst-stepper__input[disabled]{
    color: #c8c9cc;
    background-color: transparent;
}
