@charset "utf-8";
.jst-button {
  display: inline-block;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: #FFFFFF;
  //border: 1px solid #DCDFE6;
  //border-color: #DCDFE6;
  //color: #606266;
  border: 1px solid #c3c3c3;
  border-color: #c3c3c3;
  color: #000;
  -webkit-appearance: none;
  text-align: center;
  box-sizing: border-box;
  outline: none;
  margin: 0;
  transition: .1s;
  font-weight: 500;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  padding: 12px 20px;
  font-size: 14px;
  border-radius: 4px;
}
//.jst-button + .jst-button {
//  margin-left: 10px;
//}
.jst-button.is-round {
  padding: 12px 20px;
}
.jst-button:hover, .jst-button:focus {
  //color: $primary-color;
  //border-color: #c6e2ff;
  //background-color: #ecf5ff;
}
.jst-button:active {
  //color: $primary-color-a;
  //border-color: $primary-color-a;
  color: #000;
  border-color: #c3c3c3;
  background: #f0f0f0;
  outline: none;
}
.jst-button::-moz-focus-inner {
  border: 0;
}
.jst-button [class*="iconfont"] + span {
  margin-left: 5px;
}
.jst-button.is-plain:hover, .jst-button.is-plain:focus {
  background: #f0f0f0;
  border-color: #c3c3c3;
  color: #000;
}
.jst-button.is-plain:active {
  background: #f0f0f0;
  border-color: #c3c3c3;
  color: #000;
  outline: none;
}
.jst-button.is-active {
  color: $primary-color-a;
  border-color: $primary-color-a;
}
//.jst-button.is-disabled, .jst-button.is-disabled:hover, .jst-button.is-disabled:focus {
//  color: #C0C4CC;
//  cursor: not-allowed;
//  background-image: none;
//  background-color: #FFFFFF;
//  border-color: #EBEEF5;
//}
.jst-button.is-disabled, .jst-button.is-disabled:hover, .jst-button.is-disabled:focus {

  cursor: not-allowed;
  pointer-events:none;
  opacity: 0.6;
}



.jst-button.is-disabled.jst-button--text {
  background-color: transparent;
}
//.jst-button.is-disabled.is-plain, .jst-button.is-disabled.is-plain:hover, .jst-button.is-disabled.is-plain:focus {
//  background-color: #FFFFFF;
//  border-color: #EBEEF5;
//  color: #C0C4CC;
//}
.jst-button.is-loading {
  position: relative;
  pointer-events: none;
}
.jst-button.is-loading:before {
  pointer-events: none;
  content: '';
  position: absolute;
  left: -1px;
  top: -1px;
  right: -1px;
  bottom: -1px;
  border-radius: inherit;
  background-color: rgba(255, 255, 255, 0.35);
}
.jst-button.is-round {
  border-radius: 20px;
  padding: 12px 23px;
}
.jst-button.is-circle {
  border-radius: 50%;
  padding: 12px;
}
.jst-button--primary {
  color: #FFFFFF;
  background-color: $primary-color;
  border-color: $primary-color;
}
.jst-button--primary:hover, .jst-button--primary:focus {
  background: $primary-color-h;
  border-color: $primary-color-h;
  color: #FFFFFF;
}
.jst-button--primary:active {
  background: $primary-color-a;
  border-color: $primary-color-a;
  color: #FFFFFF;
  outline: none;
}
.jst-button--primary.is-active {
  background: $primary-color-a;
  border-color: $primary-color-a;
  color: #FFFFFF;
