.jst-button--text {
  border-color: transparent;
  color: $primary-color;
  background: transparent;
  padding-left: 0;
  padding-right: 0;
}
.jst-button--text:hover, .jst-button--text:focus {
  color: $primary-color-h;
  border-color: transparent;
  background-color: transparent;
}
.jst-button--text:active {
  color: $primary-color-a;
  border-color: transparent;
  background-color: transparent;
}
.jst-button--text.is-disabled, .jst-button--text.is-disabled:hover, .jst-button--text.is-disabled:focus {
  border-color: transparent;
}

.jst-button-group {
  display: inline-block;
  vertical-align: middle;
}
.jst-button-group::before,
.jst-button-group::after {
  display: table;
  content: "";
}

.jst-button-group::after {
  clear: both;
}
.jst-button-group > .jst-button {
  float: left;
  position: relative;
}
.jst-button-group > .jst-button + .jst-button {
  margin-left: 0;
}
.jst-button-group > .jst-button.is-disabled {
  z-index: 1;
}
.jst-button-group > .jst-button:first-child {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.jst-button-group > .jst-button:last-child {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.jst-button-group > .jst-button:first-child:last-child {
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
}
.jst-button-group > .jst-button:first-child:last-child.is-round {
  border-radius: 20px;
}
.jst-button-group > .jst-button:first-child:last-child.is-circle {
  border-radius: 50%;
}
.jst-button-group > .jst-button:not(:first-child):not(:last-child) {
  border-radius: 0;
}
.jst-button-group > .jst-button:not(:last-child) {
  margin-right: -1px;
}
.jst-button-group > .jst-button:hover, .jst-button-group > .jst-button:focus, .jst-button-group > .jst-button:active {
  z-index: 1;
}
.jst-button-group > .jst-button.is-active {
  z-index: 1;
}
.jst-button-group > .el-dropdown > .jst-button {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  border-left-color: rgba(255, 255, 255, 0.5);
}
.jst-button-group .jst-button--primary:first-child {
  border-right-color: rgba(255, 255, 255, 0.5);
}
.jst-button-group .jst-button--primary:last-child {
  border-left-color: rgba(255, 255, 255, 0.5);
}
.jst-button-group .jst-button--primary:not(:first-child):not(:last-child) {
  border-left-color: rgba(255, 255, 255, 0.5);
