.jst-checkbox {
  overflow: hidden;
  user-select: none;
}
.jst-checkbox__icon,
.jst-checkbox__label {
  display: inline-block;
  vertical-align: middle;
  line-height: 20px;
}
.jst-checkbox__icon {
  height: 20px;
}
.jst-checkbox__icon .jst-icon {
  font-size: 16px;
  color: transparent;
  text-align: center;
  line-height: inherit;
  width: 20px;
  height: 20px;
  box-sizing: border-box;
  border: 1px solid #c3c3c3;
  transition: 0.2s;
  padding: 1px;
}
.jst-checkbox__icon--round .jst-icon {
  border-radius: 100%;
}
.jst-checkbox__icon--square .jst-icon {
  border-radius: 2px;
}
.jst-checkbox__icon--checked .jst-icon {
  color: #fff;
  border-color: #3aa1ff;
  background-color: #3aa1ff;
}
.jst-checkbox__icon--disabled .jst-icon {
  border-color: #c8c9cc;
  background-color: #ebedf0;
}
.jst-checkbox__icon--disabled.jst-checkbox__icon--checked .jst-icon {
  color: #c8c9cc;
}
.jst-checkbox__label {
  color: #323233;
  margin-left: 10px;
}
.jst-checkbox__label--left {
  float: left;
  margin: 0 10px 0 0;
}
.jst-checkbox__label--disabled {
  color: #c8c9cc;
}
