.jst-toast {
  display: flex;
  color: #fff;
  font-size: 14px;
  line-height: 20px;
  border-radius: 4px;
  word-break: break-all;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  box-sizing: content-box;
  background-color: rgba(51, 51, 51, 0.88);
  white-space: pre-wrap;
}
.jst-toast__container {
  position: fixed;
  top: 50%;
  left: 50%;
  max-width: 70%;
  width: fit-content;
  transform: translate(-50%, -50%);
}
.jst-toast--text {
  padding: 8px 12px;
  min-width: 96px;
}
.jst-toast--icon {
  width: 90px;
  padding: 15px;
  min-height: 90px;
}
.jst-toast--icon .jst-toast__icon {
  font-size: 48px;
}
.jst-toast--icon .jst-toast__text {
  padding-top: 5px;
}
.jst-toast__loading {
  margin: 10px 0;
}
.jst-toast--top {
  transform: translate(0, -30vh);
}
.jst-toast--bottom {
  transform: translate(0, 30vh);
}

