.jst-badge-group {
  width: 85px;
}


.jst-badge {
  display: block;
  overflow: hidden;
  user-select: none;
  word-break: break-all;
  box-sizing: border-box;
  color: #7d7e80;
  padding: 20px 12px 20px 9px;
  font-size: 14px;
  line-height: 1.4;
  background-color: #fff;
  border-left: 3px solid transparent;
}
.jst-badge__text {
  position: relative;
}
.jst-badge:active {
  background-color: #f2f3f5;
}
.jst-badge:not(:last-child)::after {
  border-bottom-width: 1px;
}
.jst-badge--select {
  color: #3aa1ff!important;
  font-weight: 500;
  border-color: #3aa1ff;
}
.jst-badge--select::after {
  border-right-width: 1px;
}
.jst-badge--select,
.jst-badge--select:active {
  background-color: #e9ebea;
}
.jst-badge .jst-info {
  right: 4px;
}
.jst-badge-default .jst-info{
  background-color: #3aa1ff!important;
}
.jst-badge-success .jst-info{
  background-color: #6bb377!important;
}
.jst-badge-warn .jst-info{
  background-color: #f08f00!important;
}
