.jst-icon--image {
  width: 1em;
  height: 1em;
}
.jst-icon__image {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  max-width: 100%;
  max-height: 100%;
  margin: auto;
}
.jst-icon__info {
  z-index: 1;
  text-align: center;
}
