.jst-field__body {
  display: flex;
  align-items: center;
}
.jst-field__body--textarea {
  min-height: 24px;
}
.jst-field__input {
  position: relative;
  display: block;
  width: 100%;
  height: 24px;
  min-height: 24px;
  padding: 0;
  margin: 0;
  line-height: inherit;
  color: #000;
  text-align: left;
  background-color: transparent;
  border: 0;
  box-sizing: border-box;
  resize: none;
}

.jst-field__input--disabled {
  color: #999;
  background-color: transparent;
  opacity: 1;
}
.jst-field__input--center {
  text-align: center;
}
.jst-field__input--right {
  text-align: right;
}
.jst-field__placeholder {
  position: absolute;
  top: 0;
  left: 0;
  color: #999;
  pointer-events: none;
}
.jst-field__placeholder--error {
  color: #f44;
}
.jst-field__icon-root {
  display: flex;
  min-height: 24px;
  align-items: center;
}
.jst-field__clear-root,
.jst-field__icon-container {
  padding: 0 10px;
  margin-right: -10px;
  line-height: inherit;
  vertical-align: middle;
}
.jst-field__clear-root,
.jst-field__icon-container,
.jst-field__button {
  flex-shrink: 0;
}
.jst-field__clear-root {
  color: #c9c9c9;
}
.jst-field__icon-container {
  color: #999;
}
.jst-field__icon-container:empty {
  display: none;
}
.jst-field__button {
  padding-left: 10px;
}
.jst-field__button:empty {
  display: none;
}
.jst-field__error-message {
  font-size: 12px;
  color: #f44;
  text-align: left;
}
.jst-field__error-message--center {
  text-align: center;
}
.jst-field__error-message--right {
  text-align: right;
}
