.jst-cell {
  position: relative;
  display: flex;
  width: 100%;
  padding: 10px 15px;
  font-size: 14px;
  line-height: 24px;
  //color: #333;
  color: #000;
  background-color: #fff;
  box-sizing: border-box;
  align-items: center;
}
.jst-cell::after {
  content: ' ';
  position: absolute;
  pointer-events: none;
  box-sizing: border-box;
  transform-origin: center;
  /* cover wechat button:after default transforn-origin */
  top: auto;
  left: 15px;
  right: 0;
  bottom: 0;
  transform: scaleY(0.5);
  border-bottom: 1px solid #eee;
}
.jst-cell--borderless::after {
  display: none;
}
.jst-cell-group {
  background-color: #fff;
}
.jst-cell__label {
  margin-top: 3px;
  font-size: 12px;
  line-height: 18px;
  color: #666666;
}
.jst-cell__value {
  overflow: hidden;
  color: #666666;
  text-align: right;
  vertical-align: middle;
}
.jst-cell__title,
.jst-cell__value {
  flex: 1;
}
.jst-cell__title:empty,
.jst-cell__value:empty {
  display: none;
}
.jst-cell__left-icon-wrap,
.jst-cell__right-icon-wrap {
  display: flex;
  height: 24px;
  font-size: 16px;
  align-items: center;
}
.jst-cell__left-icon-wrap {
  margin-right: 5px;
}
.jst-cell__right-icon-wrap {
  margin-left: 5px;
  color: #666666;
}
.jst-cell__left-icon {
  line-height: 24px;
  vertical-align: middle;
}
.jst-cell__right-icon {
  line-height: 24px;
}
.jst-cell--clickable.jst-cell--hover {
  background-color: #f2f3f5;
}
.jst-cell--required {
  overflow: visible;
}
.jst-cell--required::before {
  position: absolute;
  left: 7px;
  font-size: 14px;
  color: #f44;
  content: '*';
}
.jst-cell--center {
  align-items: center;
}
.jst-cell--large {
  padding-top: 12px;
  padding-bottom: 12px;
}
.jst-cell--large .jst-cell__title {
  font-size: 16px;
}
.jst-cell--large .jst-cell__label {
  font-size: 14px;
}






.jst-cell-group {
  background-color: #fff;
}
.jst-cell-group__title {
  color: #969799;
  padding: 15px 15px 5px;
  font-size: 14px;
  line-height: 16px;
}

.jst-cell__left-icon-wrap {
  margin-right: 5px;
  color: #4a5c6a;
}
.jst-cell__left-icon-wrap, .jst-cell__right-icon-wrap {
  min-width: 1em;
  height: 24px;
  font-size: 16px;
  line-height: 24px;
}
.jst-cell__right-icon-wrap {
  margin-left: 5px;
  color: #969799;
}
