.jst-slider {
  position: relative;
  border-radius: 999px;
  background-color: #e5e5e5;
}
.jst-slider__bar {
  position: relative;
  border-radius: inherit;
  background-color: #1989fa;
}
.jst-slider__button {
  width: 20px;
  height: 20px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
  border-radius: 50%;
  background-color: #fff;
}
.jst-slider__button-wrapper {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate3d(50%, -50%, 0);
  /* use pseudo element to expand touch area */
}
.jst-slider__button-wrapper::after {
  content: '';
  position: absolute;
  width: 200%;
  height: 200%;
  top: -50%;
  left: -50%;
}
.jst-slider--disabled {
  opacity: 0.3;
}
.jst-slider--disabled .jst-slider__bar{
  background-color: #c3c3c3;
}
.jst-slider--vertical {
  height: 100%;
  display: inline-block;
}
.jst-slider--vertical .jst-slider__button-wrapper {
  top: auto;
  bottom: 0;
  transform: translate3d(50%, 50%, 0);
}
