
.jst-loading{
  z-index:0;
  line-height:0;
  vertical-align:middle;
}
.jst-loading,
.jst-loading__spinner{
  position:relative;
  display:inline-block;
}
.jst-loading__spinner {
  z-index:-1;
  width:100%;
  height:100%;
  box-sizing:border-box;
  -webkit-animation:jst-rotate 1s linear infinite;
  animation:jst-rotate 1s linear infinite;
}
