.jst-info {
  position: absolute;
  top: -8px;
  right: 0;
  min-width: 16px;
  padding: 0 3px;
  font-family: "Microsoft JhengHei","Microsoft Yahei",Helvetica,Arial,sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  color: #fff;
  text-align: center;
  white-space: nowrap;
  background-color: #f44;
  border: 1px solid #fff;
  border-radius: 16px;
  transform: translateX(50%);
  box-sizing: border-box;
  transform-origin: 100%;
}