.jst-tag {
  color: #fff;
  font-size: 10px;
  //padding: 0.2em 0.5em;
  padding: 3.5px 6px 2px 6px;
  line-height: normal;
  border-radius: 0.2em;
  display: inline-block;
}
.jst-tag::after {
  border-color: currentColor;
  border-radius: 0.4em;
}
.jst-tag--mark {
  padding-right: 0.6em;
  border-radius: 0 0.8em 0.8em 0;
}
.jst-tag--mark::after {
  border-radius: 0 1.6em 1.6em 0;
}
.jst-tag--round {
  border-radius: 0.8em;
}
.jst-tag--round::after {
  border-radius: 1.6em;
}
.jst-tag--medium {
  font-size: 12px;
}
.jst-tag--large {
  font-size: 14px;
}
