.jst-tree-select {
  user-select: none;
  font-size: 14px;
  position: relative;
}
.jst-tree-select__nav {
  width: 35%;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  min-width: 120px;
  overflow: scroll;
  background-color: #fff;
  -webkit-overflow-scrolling: touch;
}
.jst-tree-select__nitem {
  position: relative;
  line-height: 44px;
  padding: 0 9px 0 15px;
  border-bottom: 1px solid #e7e7e7;
}
.jst-tree-select__nitem--active::after {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 3.6px;
  background-color: #3aa1ff;
  content: '';
}
.jst-tree-select__nitem--active {
  font-weight: bold;
  background-color: #e9ebea;
  color: $primary-color;
}
.jst-tree-select__nitem--disabled {
  color: #969799;
}
.jst-tree-select__nitem--disabled:active::after {
  display: none;
}
.jst-tree-select__content {
  width: 65%;
  height: 100%;
  padding-left: 15px;
  margin-left: 35%;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  background-color: #fff;
  box-sizing: border-box;
}
.jst-tree-select__item {
  position: relative;
  font-weight: bold;
  line-height: 44px;
  border-bottom: 1px solid #eee;
}
.jst-tree-select__item--active {
  color: #3aa1ff;
}
.jst-tree-select__item--disabled,
.jst-tree-select__item--disabled:active {
  color: #c8c9cc;
}
.jst-tree-select__selected {
  position: absolute !important;
  top: 0;
  right: 15px;
  bottom: 0;
  height: 24px;
  margin: auto 0;
  line-height: 24px;
}
