.jst-search {
  display: flex;
  padding: 10px 16px;
  align-items: center;
  box-sizing: border-box;
}
.jst-search__content {
  display: flex;
  flex: 1;
  background-color: #f7f8fA;
  border-radius: 2px;
  padding-left: 10px;
}
.jst-search__content--round {
  border-radius: 17px;
}
.jst-search__label {
  font-size: 14px;
  color: #323233;
  line-height: 34px;
  padding: 0 5px;
}
.jst-search .jst-cell {
  flex: 1;
  padding: 5px 10px 5px 0;
  background-color: transparent;
}
.jst-search .jst-cell__left-icon {
  color: #969799;
}
.jst-search--show-action {
  padding-right: 0;
}
.jst-search input::-webkit-search-decoration,
.jst-search input::-webkit-search-cancel-button,
.jst-search input::-webkit-search-results-button,
.jst-search input::-webkit-search-results-decoration {
  display: none;
}
.jst-search__action {
  padding: 0 10px;
  font-size: 14px;
  line-height: 34px;
  color: #323233;
}
.jst-search__action:active {
  background-color: #f2f3f5;
}
