.jst-collapse-item__title .van-cell__right-icon::before {
  transform: rotate(90deg);
  transition: 0.3s;
}
.jst-collapse-item__title::after {
  visibility: hidden;
}
.jst-collapse-item__title--expanded .van-cell__right-icon::before {
  transform: rotate(-90deg);
}
.jst-collapse-item__title--expanded::after {
  visibility: visible;
}
.jst-collapse-item__title--disabled,
.jst-collapse-item__title--disabled .van-cell__right-icon {
  color: #c3c3c3;
}
.jst-collapse-item__title--disabled:active {
  background-color: #fff;
}
.jst-collapse-item{
  //border-bottom: 1px solid #eee;
}
.jst-collapse-item:last-child{
  border-bottom: none;
}
.jst-collapse-item__wrapper {
  overflow: hidden;
  will-change: height;
  transition: height 0.3s ease-in-out;
}
.jst-collapse-item__content {
  color: #666;
  padding: 15px;
  font-size: 13px;
  line-height: 1.5;
  background-color: #fff;
}



.jst-collapse-item__title--expanded .jst-cell__right-icon-wrap{
  -webkit-transform: rotate(90deg);
  -moz-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  -o-transform: rotate(90deg);
  transform: rotate(90deg);
}
.jst-collapse-item .jst-cell__right-icon-wrap{
  transition: all 0.3s;
  -webkit-transition: all 0.3s;
  -moz-transition: all  0.3s;
  -ms-transition: all  0.3s;
  -o-transition: all  0.3s;
  transition: all  0.3s;
}


.jst-collapse.jst-hairline--top-bottom::after{
  border: 0;
}