.jst-action-sheet {
  max-height:90%!important;
  color:#333
}
.jst-action-sheet__cancel,
.jst-action-sheet__item {
  height:50px;
  font-size:16px;
  line-height:50px;
  text-align:center;
  background-color:#fff
}
.jst-action-sheet__cancel:active,
.jst-action-sheet__item:active {
  background-color: #3aa1ff;
  color: #fff;
}
.jst-action-sheet__cancel {
  height:60px
}
.jst-action-sheet__cancel:before {
  display:block;
  height:10px;
  background-color:#f8f8f8;
  content:" "
}
.jst-action-sheet__item--disabled {
  color:#c9c9c9
}
.jst-action-sheet__item--disabled:active {
  background-color:#fff
}
.jst-action-sheet__subname {
  margin-left:5px;
  font-size:12px;
  color:#7d7e80
}
.jst-action-sheet__header {
  font-size:16px;
  font-weight:500;
  line-height:44px;
  text-align:center;
}
.jst-action-sheet__close {
  position:absolute!important;
  top:0;
  right:0;
  padding:0 15px;
  font-size:18px!important;
  line-height:inherit!important;
  color:#999
}
.jst-action-sheet__content{
  padding: 20px;
}
