@charset "utf-8";
.jst-radio {
  overflow: hidden;
  user-select: none;
  height: 45px;
  line-height: 45px;
}
.jst-radio__icon,
.jst-radio__label {
  display: inline-block;
  vertical-align: middle;
  line-height: 20px;
}
.jst-radio__icon {
  height: 20px;
}
.jst-radio__icon .jst-icon {
  font-size: 14px;
  color: transparent;
  text-align: center;
  line-height: inherit;
  width: 20px;
  height: 20px;
  box-sizing: border-box;
  border: 1px solid #ccc;
  transition: 0.2s;
  padding: 1px;
  display: inline-block;
  padding-top: 0px;
}
.jst-radio__icon--round .jst-icon {
  border-radius: 100%;
}
.jst-radio__icon--checked .jst-icon {
  color: #fff;
  border-color: #3aa1ff;
  background-color: #3aa1ff;
}
.jst-radio__icon--disabled .jst-icon {
  border-color: #c8c9cc;
  background-color: #ebedf0;
}
.jst-radio__icon--disabled.jst-radio__icon--checked .jst-icon {
  color: #c8c9cc;
}
.jst-radio__label {
  color: #323233;
  margin-left: 10px;
  font-size: 14px;
}
.jst-radio__label--left {
  float: left;
  margin: 0 10px 0 0;
}
.jst-radio__label--disabled {
  color: #c3c3c3;
}
