/**	
 * Entry of basic styles	
 */
.jst-ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.jst-multi-ellipsis--l2 {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.jst-multi-ellipsis--l3 {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.jst-clearfix::after {
  content: '';
  display: table;
  clear: both;
}
.jst-hairline,
.jst-hairline--top,
.jst-hairline--left,
.jst-hairline--right,
.jst-hairline--bottom,
.jst-hairline--top-bottom,
.jst-hairline--surround {
  position: relative;
}
.jst-hairline::after,
.jst-hairline--top::after,
.jst-hairline--left::after,
.jst-hairline--right::after,
.jst-hairline--bottom::after,
.jst-hairline--top-bottom::after,
.jst-hairline--surround::after {
  content: ' ';
  position: absolute;
  pointer-events: none;
  box-sizing: border-box;
  transform-origin: center;
  /* cover wechat button:after default transforn-origin */
  top: -50%;
  left: -50%;
  right: -50%;
  bottom: -50%;
  transform: scale(0.5);
  border: 0 solid #eee;
}
.jst-hairline--top::after {
  border-top-width: 1px;
}
.jst-hairline--left::after {
  border-left-width: 1px;
}
.jst-hairline--right::after {
  border-right-width: 1px;
}
.jst-hairline--bottom::after {
  border-bottom-width: 1px;
}
.jst-hairline--top-bottom::after {
  border-width: 1px 0;
}
.jst-hairline--surround::after {
  border-width: 1px;
}
