.jst-picker {
  overflow: hidden;
  user-select: none;
  position: relative;
  background-color: #fff;
  -webkit-text-size-adjust: 100%;
  /* avoid iOS text size adjust */
}
.jst-picker__toolbar {
  display: flex;
  height: 44px;
  line-height: 44px;
  justify-content: space-between;
}
.jst-picker__cancel,
.jst-picker__confirm {
  color: #000;
  padding: 0 15px;
  font-size: 14px;
}
.jst-picker__confirm{
  color: #3aa1ff;
}
.jst-picker__cancel:active,
.jst-picker__confirm:active {
  background-color: #f2f3f5;
}
.jst-picker__title {
  max-width: 50%;
  font-size: 16px;
  font-weight: 500;
  text-align: center;
}
.jst-picker__columns {
  display: flex;
  position: relative;
}
.jst-picker__loading {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  display: flex;
  position: absolute;
  align-items: center;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.9);
}
.jst-picker__loading .van-loading,
.jst-picker__frame {
  top: 50%;
  left: 0;
  width: 100%;
  z-index: 1;
  position: absolute;
  pointer-events: none;
  transform: translateY(-50%);
  background-color: #3aa1ff;
}
.jst-picker-column {
  flex: 1;
  overflow: hidden;
  font-size: 16px;
  text-align: center;
  z-index: 10;
  position: relative;
}
.jst-picker-column__item {
  padding: 0 5px;
  color: #000;
}
.jst-picker-column__item--selected {
  font-weight: 500;
  color: #fff;
  //background-color: #3aa1ff;
}
.jst-picker-column__item--disabled {
  opacity: 0.3;
}
