.jst-password-input {
  margin: 0 15px;
  user-select: none;
  position: relative;
}
.jst-password-input:focus {
  outline: none;
}
.jst-password-input__info,
.jst-password-input__error-info {
  font-size: 14px;
  margin-top: 15px;
  text-align: center;
}
.jst-password-input__info {
  color: #666;
}
.jst-password-input__error-info {
  color: #f44;
}
.jst-password-input__security {
  width: 100%;
  height: 50px;
  display: flex;
  background-color: #fff;
}
.jst-password-input__security::after {
  border-radius: 6px;
}
.jst-password-input__security li {
  flex: 1;
  height: 100%;
  position: relative;
  font-size: 20px;
  line-height: 50px;
  text-align: center;
}
.jst-password-input__security li:not(:first-of-type)::after {
  border-left-width: 1px;
}
.jst-password-input__security i {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 10px;
  height: 10px;
  margin: -5px 0 0 -5px;
  visibility: hidden;
  border-radius: 100%;
  background-color: #000;
}
