.jst-swipe {
  overflow: hidden;
  position: relative;
  user-select: none;
}
.jst-swipe__track {
  height: 100%;
}
.jst-swipe__indicators {
  display: flex;
  position: absolute;
  left: 50%;
  bottom: 10px;
  transform: translateX(-50%);
}
.jst-swipe__indicators--vertical {
  left: 10px;
  top: 50%;
  bottom: auto;
  flex-direction: column;
  transform: translateY(-50%);
}
.jst-swipe__indicators--vertical .jst-swipe__indicator:not(:last-child) {
  margin-bottom: 6px;
}
.jst-swipe__indicator {
  border-radius: 100%;
  opacity: 0.3;
  width: 6px;
  height: 6px;
  background-color: #ebedf0;
  transition: opacity 0.2s;
}
.jst-swipe__indicator:not(:last-child) {
  margin-right: 6px;
}
.jst-swipe__indicator--active {
  opacity: 1;
  background-color: #1989fa;
}




.jst-swipe-item {
  float: left;
  height: 100%;
}