.jst-popup {
  position: fixed;
  top: 50%;
  left: 50%;
  max-height: 100%;
  overflow-y: auto;
  background-color: #fff;
  box-sizing: border-box;
  animation: ease both;
  -webkit-overflow-scrolling: touch;
  transition-timing-function: ease;
}
.jst-popup--center {
  transform: translate3d(-50%, -50%, 0);
}
.jst-popup--top {
  top: 0;
  right: auto;
  bottom: auto;
  left: 50%;
  width: 100%;
  transform: translate3d(-50%, 0, 0);
}
.jst-popup--right {
  top: 50%;
  right: 0;
  bottom: auto;
  left: auto;
  transform: translate3d(0, -50%, 0);
}
.jst-popup--bottom {
  top: auto;
  right: auto;
  bottom: 0;
  left: 50%;
  width: 100%;
  transform: translate3d(-50%, 0, 0);
}
.jst-popup--left {
  top: 50%;
  right: auto;
  bottom: auto;
  left: 0;
  transform: translate3d(0, -50%, 0);
}
//.jst-popup--bottom.jst-popup--safe {
//  padding-bottom: 34px;
//}
.jst-popup--left .jst-popup__safe-top,
.jst-popup--right .jst-popup__safe-top,
.jst-popup--top .jst-popup__safe-top {
  height: 44px;
}
.jst-popup--center .jst-popup__safe-top,
.jst-popup--bottom .jst-popup__safe-top {
  padding-top: 0 !important;
}
.jst-scale-enter-active,
.jst-scale-leave-active {
  //transition-property: opacity, transform;
  transition: all .3s ease;
}
.jst-scale-enter,
.jst-scale-leave-to {
  opacity: 0;
  transform: translate3d(-50%, -50%, 0) scale(0.7);
}
.jst-fade-enter-active,
.jst-fade-leave-active {
  //transition-property: opacity;
  transition: all .3s ease;
}
.jst-fade-enter,
.jst-fade-leave-to {
  opacity: 0;
}
.jst-center-enter-active,
.jst-center-leave-active {
  //transition-property: opacity;
  transition: all .3s ease;
}
.jst-center-enter,
.jst-center-leave-to {
  opacity: 0;
}
.jst-bottom-enter-active,
.jst-bottom-leave-active,
.jst-top-enter-active,
.jst-top-leave-active,
.jst-left-enter-active,
.jst-left-leave-active,
.jst-right-enter-active,
.jst-right-leave-active {
  //transition-property: transform;
  transition: all .3s ease;
}
.jst-bottom-enter,
.jst-bottom-leave-to {
  transform: translate3d(-50%, 100%, 0);
}
.jst-top-enter,
.jst-top-leave-to {
  transform: translate3d(-50%, -100%, 0);
}
.jst-left-enter,
.jst-left-leave-to {
  transform: translate3d(-100%, -50%, 0);
}
.jst-right-enter,
.jst-right-leave-to {
  transform: translate3d(100%, -50%, 0);
}
