.jst-pagination {
  display: flex;
  text-align: center;
  line-height: 40px;
  font-size: 14px;
}
.jst-pagination__item {
  flex: 1;
  color: #000000;
  height: 40px;
  min-width: 36px;
  background-color: #fff;
  box-sizing: border-box;
  user-select: none;
}
.jst-pagination__item:active {
  color: #fff;
  background-color: #1989fa;
}
.jst-pagination__item::after {
  border-width: 1px 0 1px 1px;
  border: none;
}
.jst-pagination__item:last-child::after {
  border-right-width: 1px;
}
.jst-pagination__item--disabled,
.jst-pagination__item--disabled:active {
  background-color: #f8f8f8;
  color: #7d7e80;
  opacity: 0.6;
}
.jst-pagination__item--active {
  color: #fff;
  background-color: #1989fa;
}
.jst-pagination__prev,
.jst-pagination__next {
  padding: 0 5px;
}
.jst-pagination__next {
  border-left: 1px solid #e7e7e7;
}
.jst-pagination__page {
  flex-grow: 0;
  border-left: 1px solid #e7e7e7;
}
.jst-pagination__page-desc {
  flex: 1;
  height: 40px;
  color: #000;
  background-color: #fff;
  //border-left: 1px solid #e7e7e7;
  //border-right: 1px solid #e7e7e7;
}
.jst-pagination--simple .jst-pagination__prev::after,
.jst-pagination--simple .jst-pagination__next::after {
  border-width: 1px;
}
