














































.jst-row {
    width: 100%;
    text-align: left;
    display: flex;
    display: -webkit-flex;
    box-align: center;
    align-items: center;
    .jst-col-item {
        flex: 1;
        -webkit-flex: 1;
        min-width: 20px;
        width: 0%;
        &:first-child {
            margin-left: 0!important;
            margin-top: 0!important;
        }
    }
}
.jst-flex-row {
    box-direction: row;
    box-orient: horizontal;
    flex-direction: row;
}
.jst-flex-col {
    box-orient: vertical;
    flex-direction: column;
    > .jst-col-item {
        width: 100%;
    }
}
