.jst-tabbar-item {
  flex: 1;
  display: flex;
  color: #7d7e80;
  font-size: 12px;
  line-height: 1;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
.jst-tabbar-item__icon {
  position: relative;
  font-size: 18px;
  margin-bottom: 5px;
}
.jst-tabbar-item__icon .jst-icon {
  display: block;
  min-width: 1em;
}
.jst-tabbar-item__icon--dot::after {
  top: 0;
  right: -8px;
  width: 8px;
  height: 8px;
  content: ' ';
  position: absolute;
  border-radius: 100%;
  background-color: #f44;
}
.jst-tabbar-item__icon img {
  display: block;
  height: 18px;
}
.jst-tabbar-item--active {
  color: $primary-color;
}
.jst-tabbar-item .jst-info {
  margin-top: 2px;
}



.jst-tabbar {
  display: flex;
  width: 100%;
  height: 50px;
  background-color: #fff;
}
.jst-tabbar--fixed {
  left: 0;
  bottom: 0;
  position: fixed;
}
