.jst-nav-bar {
  position: relative;
  user-select: none;
  text-align: center;
  height: 46px;
  line-height: 46px;
  background-color: #4a5c6a;
}
.jst-nav-bar .jst-icon {
  color: #1989fa;
  vertical-align: middle;
}
.jst-nav-bar__arrow {
  min-width: 1em;
  font-size: 14px;
  color: #fff;
  top: 1px;
}
.jst-nav-bar__arrow + .jst-nav-bar__text {
  margin-left: -20px;
  padding-left: 25px;
}
.jst-nav-bar--fixed {
  top: 0;
  left: 0;
  width: 100%;
  position: fixed;
}
.jst-nav-bar__title {
  margin: 0 auto;
  max-width: 60%;
  color: #fff;
  font-size: 16px;
  font-weight: 500;
}
.jst-nav-bar__left,
.jst-nav-bar__right {
  bottom: 0;
  font-size: 14px;
  position: absolute;
}
.jst-nav-bar__left {
  left: 15px;
}
.jst-nav-bar__right {
  right: 15px;
}
.jst-nav-bar__text {
  color: #fff;
  margin: 0 -15px;
  padding: 0 15px;
  display: inline-block;
  vertical-align: middle;
}
.jst-nav-bar__text:active {
  background-color: #f2f3f5;
}
