.jst-dialog {
  position: fixed;
  top: 50%;
  left: 50%;
  overflow: hidden;
  width: 85%;
  font-size: 16px;
  transition: 0.3s;
  border-radius: 4px;
  background-color: #fff;
  transform: translate3d(-50%, -50%, 0);
  backface-visibility: hidden;
}
.jst-dialog__header {
  text-align: center;
  font-weight: 500;
  padding-top: 25px;
}
.jst-dialog__header--isolated {
  padding: 25px 0;
}
.jst-dialog__message {
  padding: 25px;
  font-size: 14px;
  line-height: 1.5;
  max-height: 60vh;
  overflow-y: auto;
  text-align: center;
  -webkit-overflow-scrolling: touch;
  //white-space: pre-wrap;
}
.jst-dialog__message--has-title {
  color: #000;
  padding-top: 12px;
}
.jst-dialog__message--left {
  text-align: left;
}
.jst-dialog__message--right {
  text-align: right;
}
.jst-dialog__footer {
  overflow: hidden;
  user-select: none;
  //border-top: 1px solid #ddd;
  //border-top: 1px solid #e7e7e7;
}
.jst-dialog__footer--buttons {
  display: flex;
}
.jst-dialog__footer--buttons .jst-button {
  flex: 1;
}
.jst-dialog .jst-button {
  border: 0;
}
.jst-dialog__confirm,
.jst-dialog__confirm:active {
  color: #3aa1ff;
}
.jst-dialog-bounce-enter {
  opacity: 0;
  transform: translate3d(-50%, -50%, 0) scale(0.7);
}
.jst-dialog-bounce-leave-active {
  opacity: 0;
  transform: translate3d(-50%, -50%, 0) scale(0.9);
}
.jst-dialog-popup .jst-popup{
  overflow: inherit;
  background-color: rgba(0, 0, 0, 0);
}
.center{
  width: 100%!important;
}
